<template>
    <div class="form">
        <h1 class="md-title"></h1>
        <md-field md-clearable>
            <label></label>
            <md-input v-model="login" ></md-input>
        </md-field>
        <md-field md-clearable>
            <label></label>
            <md-input v-model="password" type="password"></md-input>
        </md-field>
        <div class="buttons-row">
            <md-button class="md-raised left" @click="auth()"></md-button>
            <md-button class="right"> ?</md-button>
        </div>
        <div v-if="error">
            <p>{{error}}</p>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Authorization',
    data() {
        return {
            login: null,
            password: null,
            error: ''
        }
    },
    methods: {

        auth() {
            this.error = null;
            if(!this.login) {
                this.error = "  ";
                return;
            }

            if(!this.password) {
                this.error = "  ";
                return;
            }
            this.$store.dispatch("auth", { login: this.login, password: this.password })
                .then((user) => this.processAuth(user))
                .catch((error) => {
                    this.error = "   ";
                });
        },

        processAuth(user) {
            console.log(user);
            switch(user.role) {
                case "EMPLOYEE":
                    this.$router.push("/receipts");
                    break;
                case "MANAGER":
                    this.$router.push("/report");
                    break;
                case "CLIENT":
                    this.$router.push("/client-orders");
                    break;
            }
        }
    }
}
</script>

<style>
.form {
    border: solid 1px;
    text-align: center;
    width: 500px;
    padding: 15px;
    margin: auto;
}

