<template>
    <div>
        <div class="add-receipt-form">
            <div class="md-title"> </div>
            <div class="name-input">
                <div class="md-layout">
                    <div class="md-layout-item label">
                        <div class="md-subtitle"></div>
                    </div>
                    <div class="md-layout-item">
                        <md-field>
                            <md-input name="name" id="name" v-model="name"></md-input>
                        </md-field>
                    </div>
                </div>
            </div>
            <div class="description-input">
                <div class="md-layout">
                    <div class="md-layout-item label">
                        <div class="md-subtitle"></div>
                    </div>
                    <div class="md-layout-item">
                        <md-field>
                            <md-input name="description" id="description" v-model="description"></md-input>
                        </md-field>
                    </div>
                </div>
            </div>
            <div class="category selection">
                <div class="md-layout">
                    <div class="md-layout-item label">
                        <div class="md-subtitle"></div>
                    </div>
                    <div class="md-layout-item selector">
                        <md-field >
                            <md-select v-model="category" name="category" id="category">
                                <md-option value=""></md-option>
                                <md-option value=""></md-option>
                                <md-option value=""></md-option>
                            </md-select>
                        </md-field>
                    </div>
                </div>
            </div>
            <div class="instruction-input">
                <div class="md-layout">
                    <div class="md-layout-item label">
                        <div class="md-subtitle"></div>
                    </div>
                    <div class="md-layout-item">
                        <md-field>
                            <md-input name="instruction" id="instruction" v-model="instruction"></md-input>
                        </md-field>
                    </div>
                </div>
            </div>
        </div>
        <div class="table-container">
            <div class="label" v-if="ingredients.length > 0">
                <div class="md-subtitle"></div>
            </div>
            <md-list>
                <md-list-item v-for="(ingredient, index) in ingredients" v-bind:key="index">
                    <span>{{getTypeName(ingredient.type)}}</span>
                    <span>{{ingredient.name}}</span>
                    <span>{{ingredient.quantity}} .</span>
                    <span><md-button @click="removeIngredient(index)"><md-icon>close</md-icon></md-button></span>
                </md-list-item>
            </md-list>
        </div>
        <div>
            <md-button class="md-raised left" @click="addingIngredient = true"> </md-button>
        </div>
       
        <div class="buttons-row">
            <md-button class="md-raised left" :disabled="!actionEnabled" @click="addReceipt()"> </md-button>
            <md-button class="md-raised right" @click="clear()"></md-button>
        </div>
        <div v-if="resultMessage" class="result">{{resultMessage}}</div>

        <md-dialog :md-active.sync="addingIngredient">
            <div class="modal-content resource-form">
               <ResourceSelection 
                       v-on:quantity-change="quantityChanged"
                       v-on:type-change="typeChanged"
                       v-on:name-change="nameChanged"
                       v-on:id-change="idChanged"
               />
            </div>
            <md-dialog-actions>
                <md-button class="md-primary" @click="cancelAddIngredient()"></md-button>
                <md-button class="md-primary" @click="finishAddIngredient()"></md-button>
            </md-dialog-actions>
        </md-dialog>
    </div>
</template>

<script>
    import ResourceSelection from "./ResourceSelection";
    export default {
        name: "AddReceipt",
        components: {ResourceSelection},
        data() {
            return {
                name: "",
                description: "",
                instruction: "",
                category: "",
                ingredients:[],
                resultMessage: "",
                addingIngredient: false,
                ingredientForm: {
                    type: "",
                    name: "",
                    id: -1,
                    quantity: 0
                }
            }
        },
        
        computed: {
            actionEnabled() {
                return this.category && this.description && this.instruction && this.name && this.ingredients.length > 0;
            }
        },
        
        methods: {
            clear() {
                this.name= "";
                this.description = "";
                this.instruction = "";
                this.category = "";
                this.ingredients = [];
                this.resultMessage = "";
            },

            finishAddIngredient() {
                this.ingredients.push({
                    type: this.ingredientForm.type,
                    name: this.ingredientForm.name,
                    id: this.ingredientForm.id,
                    quantity: this.ingredientForm.quantity
                });

                this.addingIngredient = false;
                this.ingredientForm= {
                    type: "",
                    name: "",
                    id: -1,
                    quantity: 0
                };
            },

            cancelAddIngredient() {
                this.addingIngredient = false;
                this.ingredientForm= {
                    type: "",
                    name: "",
                    id: -1,
                    quantity: 0
                };
            },
            
            removeIngredient(index) {
              this.ingredients.splice(index);  
            },
            
            quantityChanged(value) {
                this.ingredientForm.quantity = value;
            },
            
            typeChanged(value) {
                this.ingredientForm.type = value;
            },
            
            nameChanged(value) {
                this.ingredientForm.name = value;    
            },
            
            idChanged(value) {
                this.ingredientForm.id = value;
            },
            
            getTypeName(value) {
                switch (value) {
                    case "animals": return "";
                    case "grass": return "";
                    case "runes": return "";
                }
            },
            
            addReceipt() {
                let request = {
                    name: this.name,
                    description: this.description,
                    instruction: this.instruction,
                    ingredients: this.ingredients
                };

                this.$store.dispatch("addReceipt", request)
                    .then(() => {
                        let res = `   ${this.name}.`;
                        this.clear();
                        this.resultMessage = res;
                    })
                    .catch((error) => {
                        console.log(error);
                        this.resultMessage = "";
                    });
            }
        }
    }
</script>

<style scoped>
    .add-receipt-form {
        max-width: 500px;
        margin-top: 10%;
        margin-left: 15%;
        text-align: center;
    }
    .label {
        margin: auto;
        text-align: left;
        max-width: 200px;
    }
    
    .buttons-row {
        text-align: right;
    }
    
    .resource-form {
        margin: 10px;
    }
    
    .result {
        max-width: 500px;
        margin-left: 15%;
        text-align: center;
    }
</style>