<template>
    <div>
        <md-table v-model="data.reportData" class="main-table" md-card>
            <md-table-toolbar>
                <h1 class="md-title"> </h1>
            </md-table-toolbar>
            <md-table-row slot="md-table-row" slot-scope="{ item }" >
                <md-table-cell md-label="">{{ item.name }}</md-table-cell>
                <md-table-cell md-label="">{{ item.value }}</md-table-cell>
            </md-table-row>
        </md-table>
        <h1 class="md-title">  </h1>
        <div class="charts">
            <div v-for="(chart,index) in data.charts" v-bind:key="index">
                <div class="employees-chart report-chart">
                    <line-chart :chart-data="chart"></line-chart>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
import LineChart from '@/charts/LineChart.js'

const fakeData = {
    reportData: [
        {
            name: ' ',
            value: 5
        },
        {
            name: '  ',
            value: 5
        },
        {
            name: '  ',
            value: 5
        },
        {
            name: '  ',
            value: 14
        },
        {
            name: '  ',
            value: 3
        }],
    charts: [
        {
            labels: ['', '', '', '', ''],
            datasets: [
                {
                    label: ' ',
                    backgroundColor: '#0f52bf',
                    data: [1,2,3,3,5]
                }]
        },
        {
            labels: ['', '', '', '', ''],
            datasets: [
                {
                    label: '  ',
                    backgroundColor: '#95a014',
                    data: [1,3,10,7,3]
                }
            ]
        }
    ],
    
};

export default {
    name: 'BaseReport',
    props:['type', 'employee', 'period'],
    components: {
      LineChart
    },
    
    watch: {
        employee: function(value) {
            this.fetchData();
        },

        period: function(value) {
            this.fetchData();
        },
    },
    
    mounted() {
        this.fetchData();
    },
    data() {
        return {
           data: {
               reportData: [],
               charts: []
           }
        }
    },
    methods: {
        fetchData() {
               this.$store.dispatch("getBaseReport", {type: this.type, employeeId: this.employee, period: this.period})
                   .then((data) => {
                       this.data = JSON.parse(JSON.stringify(fakeData));
                       this.data.reportData.push({name: this.employee || this.type, value: this.period || this.type});
                   })
                   .catch(console.log)
        }
    }
}
</script>

<style>
.report-chart {
    max-width: 500px;
}
</style>
